import React, {useEffect, useState} from 'react';
import Button from '@material-ui/core/Button';
import Utils from '../Utils';
import DatePicker from '../components/DatePicker';

const ONE_DAY_IN_MS = 86400 * 1000;
const INTERVAL_BETWEEN_POSTS_IN_MS = 10 * 60000; // 10 mins
const FIX_TIME_OFFSET = -7 * 60 * 60000; // GMT+7
const MIN_SCHEDULED_TIME_INTERVAL = 20 * 60000; // must schedule at least 20 mins in advance
const NUMBER_OF_DAYS = 7;
const SLOTS = [
  { hrs: 6,  min: 0,  priority: 3 },
  { hrs: 6,  min: 20, priority: 2 },
  { hrs: 6,  min: 40, priority: 1 },
  { hrs: 7,  min: 0,  priority: 1 },
  { hrs: 19, min: 0,  priority: 3 },
  { hrs: 19, min: 30, priority: 4 },
  { hrs: 20, min: 0,  priority: 7 },
  { hrs: 20, min: 30, priority: 7 },
  { hrs: 20, min: 45, priority: 4 },
  { hrs: 21, min: 0,  priority: 9 },
  { hrs: 21, min: 15, priority: 4 },
  { hrs: 21, min: 30, priority: 8 },
  { hrs: 21, min: 50, priority: 4 },
  { hrs: 22, min: 10, priority: 1 },
].map(s => ({...s, timeInDay: (s.hrs*3600 + s.min*60)*1000 }));

const getSuggestedSlot = (_timeslots, takenSlots, feedback) => {
  const userSuggestedTime = getDayFromFeedback(feedback);
  const timeslots = JSON.parse(JSON.stringify(_timeslots));
  for (const {slotsOfDay} of timeslots) {
    slotsOfDay.sort((a, b) => (a.priority < b.priority) ? 1 : -1);
    for (const {time} of slotsOfDay) {
      if (
        !takenSlots[time]
        && time > userSuggestedTime
      ) return time;
    }
  }
  return null;
};

const getDayFromFeedback = (feedback) => {
  const matched = feedback.match(/([0-9]+)\/([0-9]+)/);
  console.log(matched);
  if (!matched) return 0;
  const d = new Date(2021, parseInt(matched[2]) - 1, parseInt(matched[1]), 0, 0, 0, 0);
  d.setTime(d.getTime() + d.getTimezoneOffset()*60000 - FIX_TIME_OFFSET)
  console.log(d);
  return d.getTime();
};

const generateTimeslots = () => {
  const now = Date.now();
  const beginOfToday = Math.floor(now / ONE_DAY_IN_MS) * ONE_DAY_IN_MS;

  const timeslots = [];
  let day = 0;
  while (timeslots.length < NUMBER_OF_DAYS) {
    const startAt = beginOfToday + day*ONE_DAY_IN_MS + FIX_TIME_OFFSET;
    const slotsOfDay = [];
    for (const s of SLOTS) {
      const time = startAt + s.timeInDay;
      if (time - now > MIN_SCHEDULED_TIME_INTERVAL) {
        slotsOfDay.push({...s, time});
      }
    }
    if (slotsOfDay.length > 2) timeslots.push({
      startAt: startAt - FIX_TIME_OFFSET,
      slotsOfDay,
    });
    day++;
  }

  return timeslots;
};

const getTakenSlotList = (stat, timeslots) => {
  const res = {};
  /**
   *    +- INTERVAL_BETWEEN_POSTS_IN_MS
   *                 <-x---|----->
   * --|---------------|---|--
   * time             post time
   */
  for (const {slotsOfDay} of timeslots) {
    for (const {time} of slotsOfDay) {
      const isSlotTaken = stat.some(scheduledTime =>
        scheduledTime - INTERVAL_BETWEEN_POSTS_IN_MS < time
        && time < scheduledTime + INTERVAL_BETWEEN_POSTS_IN_MS
      );
      if (isSlotTaken) res[time] = true;
    }
  }
  return res;
};

const ScheduledList = ({stat}) => {
  const statAsc = JSON.parse(JSON.stringify(stat)).sort();
  return <>
    <p><b>Các bài đăng đã lên lịch</b></p>
    <ul>
      {statAsc.map(s => {
        return <li key={s}>{Utils.getTimeStr(s)} ngày {Utils.getDateStr(s)}</li>
      })}
    </ul>
  </>;
};

// statArr = [int, int...]
export const TimeslotPicker = ({ statArr, value, onChange, feedback }) => {
  const [useOldPicker, setUseOldPicker] = useState(false);
  const [showMoreSlots, setShowMoreSlots] = useState(false);
  const [timeslots] = useState(generateTimeslots());
  const [takenSlots, setTakenSlots] = useState({});
  const [suggestedSlot, setSuggestedSlot] = useState(null);
  const val = value * 1000;

  useEffect(() => {
    const stat = JSON.parse(JSON.stringify(statArr));
    setTakenSlots(getTakenSlotList(stat, timeslots));
    const suggested = getSuggestedSlot(timeslots, takenSlots, feedback);
    if (suggested) {
      setSuggestedSlot(suggested);
      setTimeout(() => onChange(Math.round(suggested/1000)), 200);
    }
  // eslint-disable-next-line
  }, [statArr]);

  return <>
    <p>
      <b>Lên lịch bài đăng</b>{'   '}
      <Button
        variant='outlined'
        size='small'
        onClick={() => setUseOldPicker(!useOldPicker)}
      >{useOldPicker ? 'Chọn giờ theo slot' : 'Chọn giờ thủ công'}</Button>
    </p>

    {feedback && <p>
      Góp ý từ người gửi: {feedback}
    </p>}

    {!useOldPicker && <>
      {suggestedSlot && <p>
        Giờ đăng bài do máy gợi ý: {Utils.getTimeStr(suggestedSlot, 7)} ngày {Utils.getDateStr(suggestedSlot, 7)}
      </p>}
      {timeslots.map((day, i) => (!showMoreSlots && i < 3) || showMoreSlots
        ? <p key={day.startAt}>
          <b>{Utils.getDateStr(day.startAt, 7)}</b> (GMT+7)<br/>
          {day.slotsOfDay.map(({time}) => <React.Fragment key={time}>
            <Button
              variant='outlined'
              size='small'
              color='primary'
              className={val === time ? 'timeslot-selected' : ''}
              onClick={() => onChange(Math.round(time/1000))}
              disabled={takenSlots[time]}
            >{Utils.getTimeStr(time, 7)}</Button>
            {'  '}
          </React.Fragment>)}
        </p> : null
      )}
      {!showMoreSlots && <Button
        variant='outlined'
        size='small'
        color='primary'
        onClick={() => setShowMoreSlots(true)}
      >+ Hiện thêm ngày</Button>}
    </>}

    {useOldPicker && <>
      <DatePicker
        handleDateChange={(date) => onChange(Math.round(date))}
      />
      <ScheduledList stat={statArr} />
    </>}
  </>
}
