import React from 'react';
import {
  TextInput,
  MultipleCheckboxes,
  MultipleCheckboxesLimitedHeight,
  LongTextInput,
  UploadFileInput,
  YesOrNo,
} from './FormField';
import { VN_CITIES } from './data';
import Utils from '../Utils';
import { CHAT_SPEECH_TYPE_REGEX } from './FormConfirmation';

export const FormQuestions = [
  {
    element: (props) => (
      <UploadFileInput
        title="Ảnh cho bài đăng"
        subtitle={<>
          Ảnh này sẽ được dùng để đăng lên fanpage, thường sẽ là avatar của dự án.<br/>
          Lưu ý:<br/>
          - Chúng mình KHÔNG CHẤP NHẬN ảnh chụp tập thể của BTC<br/>
          - Không chấp nhận các ảnh dễ gây hiểu nhầm, hoặc có nội dung không lành mạnh<br/>
          - Ảnh nên có tỷ lệ vuông (1:1) để hiện tốt trên cả instagram và facebook<br/>
        </>}
        required={true}
        {...props}
      />
    ),
    name: 'image',
    defaultValue: () => null,
    checkError: (value) => {
      if (!value) return 'Bạn chưa chọn ảnh để đăng lên page chúng mình';
    },
    populateToDoc: (value, doc) => {
      doc['image'] = [value];
    },
  },

  {
    element: (props) => (
      <TextInput
        title="Tên dự án/cuộc thi/sự kiện"
        subtitle="Hãy nhập 1 tên ngắn gọn cho dự án, cuộc thi hay sự kiện của bạn nhé"
        placeholder="Ví dụ: ABC Project..."
        required={true}
        {...props}
      />
    ),
    name: 'name',
    defaultValue: () => '',
    checkError: (value) => {
      if (!value.trim()) return 'Bạn chưa điền tên dự án/cuộc thi/sự kiện';
    },
    populateToDoc: (value, doc) => {
      doc['name'] = value.trim();
    },
  },

  {
    element: (props) => (
      <MultipleCheckboxes
        title="Phân loại"
        subtitle="Bạn có thể thêm phân mục dự án của bạn (nếu không có trong danh sách này) dưới dạng hashtag"
        moreLabel="Hoặc nhập thêm các hashtag khác..."
        placeholder="Ví dụ: #dự_án_ABC"
        required={true}
        {...props}
      />
    ),
    name: 'type',
    defaultValue: () => {
      const cls = Utils.getParameterByName('cls') || '';
      const val = {
        '#dự_án_giáo_dục': false,
        '#dự_án_tình_nguyện': false,
        '#dự_án_văn_hóa': false,
        '#dự_án_nghệ_thuật': false,
        '#dự_án_khoa_học': false,
        '#dự_án_môi_trường': false,
        '#cuộc_thi': !!cls.match(/contest/),
        '#sự_kiện': !!cls.match(/event/),
        'other': '',
      };
      return val;
    },
    checkError: (value) => {
      const atLeastOneFilled = Object.values(value).some(t => !!t);
      const hasOther = !!value.other.trim();
      const otherHasHash = !!value.other.match(/#/);
      if (!atLeastOneFilled) return 'Bạn chưa chọn phần này';
      if (hasOther && !otherHasHash) return 'Phần ô điền phải có dấu hashtag, ví dụ #dự_án_ABC';
    },
    populateToDoc: (value, doc) => {
      let cls = Utils.getParameterByName('cls') || 'project';
      if (cls.match(/project/)) cls = 'project';
      else if (cls.match(/contest/)) cls = 'contest';
      else if (cls.match(/event/)) cls = 'event';
      else cls = 'project';
      const arr = Object.entries(value)
        .filter(([key, value]) => key !== 'other' && value)
        .map(a => a[0]);
      const other = value.other.trim();
      if (!!other) {
        arr.push(other);
      }
      doc['cls'] = cls;
      doc['type'] = arr;
    },
  },

  {
    element: (props) => (
      <LongTextInput
        title="Mô tả về dự án/cuộc thi/sự kiện"
        subtitle={<>
          Lưu ý:<br/>
          - Hãy viết thật chi tiết, để người đọc có thể hiểu dự án/cuộc thi/sự kiện của bạn hướng đến điều gì nhé.<br/>
          - KHÔNG copy-paste link bài hỗ trợ truyền thông (phải viết đầy đủ nội dung vào đây)<br/>
          - Các thông tin về nhà tài trợ sẽ bị xóa sau khi kiểm duyệt.<br/>
          - Bạn cũng có thể để link đơn đăng ký (nếu có) trong phần mô tả<br/>
        </>}
        placeholder="Ví dụ: ABC Project là dự án được lập ra vì mục đích... Chúng mình hướng tới việc..."
        required={true}
        rows={15}
        {...props}
      />
    ),
    name: 'description',
    defaultValue: () => '',
    checkError: (value) => {
      const v = value.trim();
      if (!v) return 'Bạn chưa điền phần mô tả này';
      if (v.length < 400) return 'Phần mô tả cho dự án quá ngắn. Bạn hãy viết 1 cách chi tiết hơn (dự án của bạn làm gì, có ý nghĩa thế nào,...). Hãy viết chi tiết, KHÔNG copy-paste link bài viết';
      // eslint-disable-next-line
      const urls = v.match(/https?:\/\/(www\.)?[-a-zA-Z0-9@:%._\+~#=]{1,256}\.[a-zA-Z0-9()]{1,6}\b([^ \n\r\t]+)/gi);
      if (urls) {
        for (const url of urls) {
          if (url.match(/\.\.\./)) return `Do phần mô tả bạn copy từ facebook, nên link sau: "${url}" đã bị facebook rút ngắn (có dấu 3 chấm). Vì vậy, độc giả sẽ không thể bấm vào được. Bạn nên đặt lại link gốc, hoặc dùng bit.ly hay bom.to để rút gọn link nhé!`;
        }
      }
    },
    populateToDoc: (value, doc) => {
      doc['description'] = value.trim();
    },
  },

  {
    element: (props) => (
      <MultipleCheckboxesLimitedHeight
        title="Tỉnh, thành phố"
        subtitle="Nơi dự án / cuộc thi / sự kiện của bạn sẽ diễn ra. Bạn có thể chọn nhiều tỉnh, thành phố (hãy cuộn sang ngang để tìm)"
        required={true}
        {...props}
      />
    ),
    name: 'locations',
    defaultValue: () => {
      const val = {};
      for (const c of VN_CITIES) val[c] = false;
      return val;
    },
    checkError: (value) => {
      const atLeastOneFilled = Object.values(value).some(t => !!t);
      if (!atLeastOneFilled) return 'Bạn chưa chọn phần này';
    },
    populateToDoc: (value, doc) => {
      const arr = Object.entries(value)
        .filter(a => a[1])
        .map(a => a[0]);
      doc['locations'] = arr;
    },
  },

  {
    element: (props) => (
      <TextInput
        title="Link facebook page"
        subtitle="Hãy nhập link tới trang facebook chính của dự án, cuộc thi hay sự kiện của bạn nhé"
        placeholder="Ví dụ: https://www.facebook.com/tonghopevent"
        required={true}
        {...props}
      />
    ),
    name: 'linkfb',
    defaultValue: () => '',
    checkError: (value) => {
      const v = value.trim();
      if (!v) return 'Bạn chưa điền mục này';
      if (!v.match(/(fb|facebook)\.com/)) return 'Link bạn nhập không đúng định dạng. Ví dụ, link tới facebook của chúng mình phải là: https://www.facebook.com/tonghopevent';
    },
    populateToDoc: (value, doc) => {
      doc['linkfb'] = value;
    },
  },

  {
    element: (props) => (
      <YesOrNo
        title="Dự án của bạn có tuyển nhân sự không?"
        subtitle="Ví dụ như tuyển TNV, tuyển Ban điều hành, người tham gia,..."
        required={true}
        {...props}
      />
    ),
    name: '_recruitment',
    defaultValue: () => null,
    checkError: (value) => {
      if (value === null) return 'Bạn chưa chọn mục này';
    },
    populateToDoc: (value, doc) => null,
  },

  {
    dependsOn: '_recruitment',
    element: (props) => (
      <MultipleCheckboxes
        title="Yêu cầu đối tượng"
        moreLabel="Hoặc các đối tượng khác... (không bắt buộc điền)"
        placeholder="Khác..."
        {...props}
      />
    ),
    name: 'wanted',
    defaultValue: () => ({
      'Ban điều hành': false,
      'Tình nguyện viên': false,
      'Người tham gia': false,
      'other': '',
    }),
    checkError: (value) => null,
    populateToDoc: (value, doc) => {
      const arr = Object.entries(value)
        .filter(([key, value]) => key !== 'other' && value)
        .map(a => a[0]);
      if (value.other) arr.push(value.other);
      doc['wanted'] = arr;
    },
  },

  {
    dependsOn: '_recruitment',
    element: (props) => (
      <LongTextInput
        title="Deadline tuyển nhân sự"
        subtitle="(Không bắt buộc) Nếu bạn có deadline riêng cho từng ban, bạn cũng có thể ghi rõ trong phần này nhé"
        placeholder="..."
        rows={2}
        {...props}
      />
    ),
    name: 'deadline',
    defaultValue: () => '',
    checkError: (value) => {
      if (!value) return null;
      if (value.match(CHAT_SPEECH_TYPE_REGEX)) {
        return 'Bạn có viết những từ như "dạ" hay "ạ". Phần mềm sẽ copy-paste trực tiếp câu bạn đã nhập lên bài đăng. Vậy nên, bạn hãy xóa những câu như vậy đi nhé';
      }
      return null;
    },
    populateToDoc: (value, doc) => doc['deadline'] = value.trim(),
  },

  {
    dependsOn: '_recruitment',
    element: (props) => (
      <LongTextInput
        title="Quyền lợi thành viên khi tham gia dự án"
        subtitle="(Không bắt buộc) VD: như được chứng chỉ, được giao lưu, học hỏi kinh nghiệm về lĩnh vực gì,..."
        placeholder="VD: Tham gia ABC Project, bạn sẽ nhận được... và ngoài ra..."
        rows={7}
        {...props}
      />
    ),
    name: 'benefit',
    defaultValue: () => '',
    checkError: (value) => {
      if (!value) return null;
      if (value.match(CHAT_SPEECH_TYPE_REGEX)) {
        return 'Bạn có viết những từ như "dạ" hay "ạ". Phần mềm sẽ copy-paste trực tiếp câu bạn đã nhập lên bài đăng. Vậy nên, bạn hãy xóa những câu như vậy đi nhé';
      }
      return null;
    },
    populateToDoc: (value, doc) => doc['benefit'] = value.trim(),
  },

  // Thông tin thêm

  {
    element: (props) => (
      <TextInput
        title="Bạn có lưu ý, câu hỏi gì cho admin không?"
        subtitle={<>
          (Không bắt buộc) Ví dụ: muốn đăng bài vào ngày nào,...<br/>
          (Ô điền này là cho admin đọc, nó sẽ không được ghi vào bài đăng lên facebook)
        </>}
        placeholder="..."
        {...props}
      />
    ),
    name: 'feedback',
    defaultValue: () => '',
    checkError: (value) => null,
    populateToDoc: (value, doc) => doc['feedback'] = value.trim(),
  },

];