import React from 'react';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import FormControl from '@material-ui/core/FormControl';
import FormGroup from '@material-ui/core/FormGroup';
import Button from '@material-ui/core/Button';
import { UploadButton } from './FormUploadButton';

const classes = {
  input: {
    width: '500px',
    maxWidth: '100%',
    marginBottom: '0.1em',
  },
  container: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  cardStyle: {
    maxWidth: '700px',
    marginTop: '20px',
    marginRight: 'auto',
    marginLeft: 'auto',
  }
}

const FormField = ({title, subtitle, required, children, error}) => {
  return (
    <Card style={classes.cardStyle}>
      <CardContent>
        <Typography variant="h6" component="h6" gutterBottom={true}>
          {title}
          {required && <span className="required"> *</span>}
        </Typography>
        {error && <p className="error">{error}</p>}
        <Typography variant="body1" component="small" color="textSecondary">
          {subtitle}
        </Typography>
        <FormControl component="fieldset">
          {children}
        </FormControl>
      </CardContent>        
    </Card>
  );
};

export const TextInput = ({value, title, subtitle, required, placeholder, onChange, error}) => {
  return (
    <FormField title={title} subtitle={subtitle} required={required} error={error}>
      <TextField
        type="text"
        value={value}
        style={classes.input}
        onChange={(e) => onChange(e.target.value)}
        margin="normal"
        autoComplete="false"
        placeholder={placeholder}
        variant="outlined"
      />
    </FormField>
  );
};

export const LongTextInput = ({value, title, subtitle, required, placeholder, onChange, error, rows = 10}) => {
  return (
    <FormField title={title} subtitle={subtitle} required={required} error={error}>
      <TextField
        type="text"
        value={value}
        style={{...classes.input, width: '100%'}}
        onChange={(e) => onChange(e.target.value)}
        margin="normal"
        autoComplete="false"
        placeholder={placeholder}
        variant="outlined"
        multiline
        rows={rows}
        fullWidth
      />
    </FormField>
  );
};

export const MultipleCheckboxes = ({value = {}, title, subtitle, placeholder, required, onChange, error, moreLabel}) => {
  return (
    <FormField title={title} subtitle={subtitle} required={required} error={error}>
      <FormGroup>
        {Object.keys(value).map(k => k !== 'other' ? (
          <FormControlLabel key={k} control={<Checkbox
              checked={value[k]}
              onChange={() => onChange({...value, [k]: !value[k]})}
            />}
            label={k}
          />
        ) : null)}
        {value.other !== undefined && <TextField
          label={moreLabel}
          type="text"
          value={value.other || ''}
          style={classes.input}
          onChange={(e) => onChange({...value, other: e.target.value})}
          margin="normal"
          autoComplete="false"
          placeholder={placeholder}
          variant="outlined"
        />}
      </FormGroup>
    </FormField>
  );
};

export const MultipleCheckboxesLimitedHeight = ({value = {}, title, subtitle, required, onChange, error}) => {
  return (
    <FormField title={title} subtitle={subtitle} required={required} error={error}>
      <FormGroup className="multiple-checkboxes-limited-height">
        <div>
          {Object.keys(value).map(k => (
            <FormControlLabel key={k} control={<Checkbox
                checked={value[k]}
                onChange={() => onChange({...value, [k]: !value[k]})}
              />}
              label={k}
            />
          ))}
        </div>
      </FormGroup>
    </FormField>
  );
};

export const UploadFileInput = ({value, title, subtitle, required, onChange, error}) => {
  return (
    <FormField title={title} subtitle={subtitle} required={required} error={error}>
      <UploadButton value={value} onChange={onChange} />
    </FormField>
  );
};

export const YesOrNo = ({value = null, title, subtitle, required, onChange, error}) => {
  const click = (b) => () => onChange(b);
  return (
    <FormField title={title} subtitle={subtitle} required={required} error={error}>
      <div className="yes-or-no">
        <Button variant={value === true ? "contained" : "outlined"} color="primary" onClick={click(true)}>
          Có
        </Button>
        {'   '}
        <Button variant={value === false ? "contained" : "outlined"} color="primary" onClick={click(false)}>
          Không
        </Button>
      </div>
    </FormField>
  );
};
