import React, { useState, useEffect, useCallback } from 'react';
import Theme from '../Theme';
import Config from '../Config';
import axios from 'axios';
import '../App.css';
import '../bootstrap.css';
import './styles.css';
import MuiThemeProvider from '@material-ui/core/styles/MuiThemeProvider';
import CssBaseline from '@material-ui/core/CssBaseline';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import CircularProgress from '@material-ui/core/CircularProgress';
import Button from '@material-ui/core/Button';
import { FormQuestions } from './FormQuestions';
import { checkWarning, FormConfirmation } from './FormConfirmation';
import * as Sentry from '@sentry/browser';

const FORM_SUBMIT_ENDPOINT =  'https://tonghopevent-form-submit-btpjgpbvma-uc.a.run.app/form/submit';

const classes = {
  input: {
    marginBottom: '20px',
    width: '500px',
    maxWidth: '100%'
  },
  container: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  cardStyle: {
    maxWidth: '700px',
    marginTop: '20px',
    marginRight: 'auto',
    marginLeft: 'auto',
  }
}

const getDefaultValues = () => {
  const values = {};
  for (const q of FormQuestions) {
    values[q.name] = q.defaultValue();
  }
  return values;
};

const getUserContext = () => {
  try {
    const data = decodeURIComponent(window.location.href.match(/data=([^&]+)/)[1]);
    const [hashPsid, org] = data.split(';');
    return {
      psid: hashPsid.substr(0, hashPsid.length - 5),
      hash: hashPsid.substr(-5),
      org: org.toLowerCase(),
    };
  } catch (e) {
    alert('ERROR: NO CONTEXT');
    return null;
  }
};

const submitForm = async (doc) => {
  try {
    const ctx = window.TONGHOPEVENT_CONTEXT;
    const data = {...doc, ...ctx};
    // on backend: validate & remove "hash", add "created": Date.now()
    await axios.post(FORM_SUBMIT_ENDPOINT, data);
    return true;
  } catch (e) {
    Sentry.captureException(e);
    console.error(e);
    if (e.response?.data?.error) {
      alert(`Có lỗi xảy ra, xin vui lòng chụp màn hình và báo lại với admin: ${e.response.data.error}`);
    }
    return false;
  }
};

const Form = () => {
  const [values, setValues] = useState(getDefaultValues());
  const [errors, setErrors] = useState({});
  const [hasError, setHasErrors] = useState(false);
  const [loading, setLoading] = useState(false);
  const [success, setSuccess] = useState(false);
  const [warn, setWarn] = useState(null);
  const [docRecord, setDocRecord] = useState(null);

  if (!window.TONGHOPEVENT_CONTEXT) {
    window.TONGHOPEVENT_CONTEXT = getUserContext();
    console.log(window.TONGHOPEVENT_CONTEXT);
  }

  const checkIfHasError = (errors) => {
    return Object.values(errors).some(t => !!t);
  };

  const onSubmit = useCallback(async (_doc) => {
    const isTest = window.location.href.match(/nui_test/);
    const doc = _doc || docRecord;
    if (!checkIfHasError(errors)) {
      setLoading(true);
      console.log(doc);
      if (!isTest) {
        const success = await submitForm(doc);
        setSuccess(success);
        if (!success) alert('Có lỗi xảy ra, xin vui lòng thử lại!');
      } else { // nui_test
        setSuccess(true);
      }
      setLoading(false);
    }
  }, [docRecord, errors]);

  const onPreSubmit = useCallback(() => {
    const doc = {};
    const errors = {};
    for (const q of FormQuestions) {
      const name = q.name;
      const v = values[name];
      errors[name] = q.checkError(v);
      q.populateToDoc(v, doc);
    }
    setErrors(errors);
    // make sure there is no error
    if (!checkIfHasError(errors)) {
      setDocRecord(doc);
      const warn = checkWarning(doc);
      if (warn) {
        setWarn(warn);
      } else {
        onSubmit(doc);
      }
    }
  }, [onSubmit, values]);

  useEffect(() => {
    setHasErrors(checkIfHasError(errors));
  }, [errors]);

  const onFieldChanged = useCallback((name) => (val) => {
    setValues({ ...values, [name]: val });
    if (errors[name]) {
      setErrors({ ...errors, [name]: null });
    }
  }, [values, errors]);

  return (
    <MuiThemeProvider theme={Theme}>
      <CssBaseline />
      <AppBar position="static">
        <Toolbar>
          <Typography variant="h6" style={{flexGrow: 1}}>
            Form nhập dữ liệu
          </Typography>
        </Toolbar>
      </AppBar>
      <div className="mainView">
        <Card style={classes.cardStyle}>
          <CardContent>
            <Typography variant="h5" component="h2" gutterBottom={true}>
              Form nhập dữ liệu hỗ trợ truyền thông
            </Typography>
            {!success && <Typography variant="subtitle1" color="textSecondary" gutterBottom={true}>
              Chúng mình sẽ copy các thông tin bạn nhập dưới đây và đăng lên page, vậy nên các bạn hãy chú ý viết một cách rõ ràng, dễ hiểu cho người đọc nhé!
            </Typography>}
            {!success && <Typography variant="body1" component="small" color="textSecondary">
              (Các ô có dâu * là bắt buộc)
            </Typography>}
            {success && <Typography variant="subtitle1" color="textSecondary" gutterBottom={true}>
              Dữ liệu bạn nhập đã được lưu lại.<br/>Chúng mình sẽ thông báo với bạn ngay khi bài đăng được duyệt!
            </Typography>}
          </CardContent>        
        </Card>

        {!success && FormQuestions.map(q => {
          // q does not depends on other question
          // OR q does depends on other question and that question is true
          if (!q.dependsOn || (q.dependsOn && values[q.dependsOn])) {
            const name = q.name;
            const elem = q.element({
              value: values[name],
              key: name,
              onChange: onFieldChanged(name),
              error: errors[name],
            });
            return elem;
          } else {
            return null;
          }
        })}

        {!success && <Card style={classes.cardStyle}>
          <CardContent>
            <div>
              <small>Đừng quên bấm nút "Hoàn thành" khi bạn đã điền xong nhé:<br/><br/></small>
              <Button 
                color="primary"
                variant="contained"
                onClick={onPreSubmit}
                disabled={hasError}
              >Hoàn thành</Button>
              {hasError && <p className="error">
                Vẫn còn một vài ô điền có vấn đề. Bạn hãy kiểm tra lại nhé.
              </p>}
              <br/>
              <br/>
              <small>
                ------<br/>
                😸 Jiji: Hệ thống form điền này đang trong giai đoạn thử nghiệm.<br/>Nếu bạn gặp lỗi gì, hãy inbox cho Jiji nhé!
              </small>
            </div>
          </CardContent>        
        </Card>}

        {/* Copyright */}
        <center>
          <br/><br/>
          Developed by&nbsp;
          <b><a href="https://ngxson.com" target="_blank" rel="noopener noreferrer">Nui</a></b><br/>
          Version {Config.VERSION}
          <br/><br/>
        </center>
      </div>

      {loading && <div className="loading-overlay">
        <div>
          <CircularProgress /><br/>
          Đang gửi dữ liệu...
        </div>
      </div>}

      <FormConfirmation
        warn={warn}
        onSubmit={onSubmit}
        clearWarn={() => setWarn(null)}
      />
    </MuiThemeProvider>
  );
};

export default Form;