import React, { useEffect } from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';

export const CHAT_SPEECH_TYPE_REGEX = /(^|[^a-z])d?ạ([^a-z]|$)/;
export const CHAT_SPEECH_TYPE_WARN = 'chat_speech';

const checkSpeechTypeWarning = (doc) => {
  const tmp = Object.entries(doc).filter(([k]) =>
    k === 'name'
    || k === 'description'
    || k === 'linkfb'
    || k === 'deadline'
    || k === 'benefit'
  ).map(([k, v]) => v).join('\n');
  const matched = tmp.match(CHAT_SPEECH_TYPE_REGEX);
  console.log(matched);
  return matched
    ? CHAT_SPEECH_TYPE_WARN
    : null;
};

export const checkWarning = (doc) => {
  let res = checkSpeechTypeWarning(doc);
  if (res) return res;
  return null;
};

export const FormConfirmation = ({ warn, onSubmit, clearWarn }) => {
  const [open, setOpen] = React.useState(false);

  useEffect(() => {
    if (warn) setOpen(true);
  }, [warn]);

  const handleClose = () => {
    setOpen(false);
    clearWarn();
  };

  const handleConfirm = () => {
    handleClose();
    onSubmit();
  };

  return <>
    <Dialog
      open={open}
      onClose={handleClose}
      aria-labelledby="alert-dialog-title"
      aria-describedby="alert-dialog-description"
    >
      <DialogTitle id="alert-dialog-title">Xác nhận</DialogTitle>
      <DialogContent>
        <DialogContentText id="alert-dialog-description">
          {warn === CHAT_SPEECH_TYPE_WARN && <>
            Trong phần bạn điền, bạn có viết những từ như <b>"dạ"</b> hay <b>"ạ"</b>. Xin bạn chú ý, phần mềm sẽ <b>copy-paste trực tiếp câu bạn đã nhập lên bài đăng trên page</b>, vậy nên nếu bạn có viết mấy câu như vậy, xóa đi giúp Jiji nha!<br/>
            <br/>
            Còn nếu bạn chắc là tất cả đều ổn (có thể 😸 Jiji nhầm lẫn gì đó, mình chỉ là con mèo thôi mà!), thì bạn hãy bấm nút "Xác nhận gửi" dưới đây
          </>}
        </DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button onClick={handleConfirm} color="primary" autoFocus>
          Xác nhận gửi
        </Button>
        <Button onClick={handleClose} color="primary">
          Quay lại
        </Button>
      </DialogActions>
    </Dialog>
  </>;
};